.description {
  h2 {
    font-size: 48px;
    line-height: 48px;
    font-weight: normal;
    margin: 0;
    text-transform: uppercase;
    margin-top: -5px;
    margin-bottom: 5px;
  }
}

.description__price {
  font-size: 48px;
  line-height: 56px;
  color: $red;
  margin: 20px 0;
}

.description__text {
  font-family: $roboto;
  font-size: 22px;
  line-height: 30px;
  letter-spacing: 0.2px;
  max-width: 480px;
  margin: 0;
}

.description__warranty {
  font-size: 14px;
  line-height: 18px;
  font-family: $roboto;
  color: $red;
  margin-top: 12px;
}

.description__button {
  background-color: $red;
  color: $beige-background;
  min-width: 273px;

  span {
    padding-left: 32px;
    background-image: url("../img/goods-backet-product-card.svg");
    background-repeat: no-repeat;
    background-position: left center;
  }
}

.description__ratio {
  display: flex;
  font-family: $roboto;
  font-size: 22px;
  line-height: 30px;
  letter-spacing: 0.2px;
}

.description__stars {
  width: 132px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-right: 10px;
}
